.carouselItem {
  font-family: 'Orbitron', sans-serif;
  letter-spacing: 2px;
  font-size: 16px;
  text-align: center;
  color: var(--text-color);
  text-decoration: none;
}
.carouselItem img:hover {
  transform: scale(1.05); /* Enlarge the item by 10% */
  transition: transform 0.4s ease; /* Smooth transition for the transform */
}

.carouselItem img {
  max-height: 160px; /* Adjust this value to suit your design */
  margin: 0px auto 30px auto;
  display: block; /* This removes extra space below the image */
}

@media (max-width: 1200px) {
  .carouselContainer {
    width: 100%;
  }
}
